export const CHANGE_TASK = 'CHANGE_TASK';
export const REPLAY_GAME = 'REPLAY_GAME';
export const ADD_FAIL_VERB='ADD_FAIL_VERB'
export const REMOVE_FAIL_VERB = 'REMOVE_FAIL_VERB';

export function changeTask(isTaskRight) {
  return {
    type: CHANGE_TASK,
    payload: isTaskRight
  };
}

export function addFailVerb(verbDef) {
  return {
    type: ADD_FAIL_VERB,
    payload: verbDef

  }
}

export function removeFailVerb(verbKey) {
  return {
    type: REMOVE_FAIL_VERB,
    payload: verbKey
  }
}

export function replayGame() {
  return {
    type: REPLAY_GAME
  };
}
