import React from 'react';
import { connect } from 'react-redux';
import * as actions from '../store/game/actions';
import FailHistoryItem from './FailHistoryItem';

function FailHistory({ fails, handleRemoveFailVerb }) {
  return (
    <div className="m22-fail-history d-flex flex-column flex-fill text-center">
      {Object.keys(fails).map((key, idx) => (
        <FailHistoryItem key={fails[key].base + idx} fail={fails[key]} onTouchEnded={handleRemoveFailVerb} />
      ))}
    </div>
  );
}

const mapStateToProps = state => {
  return {
    fails: state.game.fails
  };
};

const mapDispatchToProps = {
  handleRemoveFailVerb: actions.removeFailVerb
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FailHistory);
