import React from 'react';
import { MdPlayCircleOutline } from 'react-icons/md';

export default function NextButton({
  answerSimple,
  answerParticiple,
  isAnswerRight,
  onTaskChange
}) {
  const isTaskCompleted = answerSimple !== null && answerParticiple !== null;

  return (
    <div className="m22-change-task d-flex flex-column align-items-end mx-4 px-5 w-100">
      <MdPlayCircleOutline
        onClick={e => {
          onTaskChange(isAnswerRight);
        }}
        onKeyPress={e => {
          if (e.key === 'Enter') {
            onTaskChange(isAnswerRight);
          }
        }}
        className={(isTaskCompleted ? 'active' : 'disabled') + ' m22-icon'}
      />
    </div>
  );
}
