import React, { useRef } from 'react';
import { MdCancel } from 'react-icons/md';

export default function FailHistoryItem({ fail, onTouchEnded }) {
  const ulEl = useRef(null);
  let ulPositionX = 0;
  let ulStartPositionX = 0;

  const handleTouchStart = e => {
    ulStartPositionX = e.changedTouches[0].clientX;
  };

  const handleTouchMove = (e, key) => {
    if (Math.abs(ulStartPositionX - e.changedTouches[0].clientX) < 5) {
      return;
    }
    console.log(ulPositionX, window.innerWidth);
    if (ulPositionX * 1.5 >= window.innerWidth) {
      onTouchEnded(key);
      return;
    }
    ulPositionX = ulStartPositionX - e.changedTouches[0].clientX;

    ulEl.current.style.setProperty('right', ulPositionX + 'px');
  };

  const handleTouchEnd = () => {
    ulEl.current.style.setProperty('right', '0px');
  };

  return (
    <ul
      ref={ulEl}
      id={fail.base}
      className="d-flex d-row"
      onTouchStart={e => {
        handleTouchStart(e);
      }}
      onTouchMove={e => {
        handleTouchMove(e, fail.base);
      }}
      onTouchEnd={handleTouchEnd}
    >
      <li>{fail.base}</li>
      <li>{fail.simple}</li>
      <li>{fail.participle}</li>
      <li className="m22-icon d-none d-sm-block">
        <button type="button" className="btn btn-link">
          <MdCancel onClick={e => onTouchEnded(fail.base)} />
        </button>
      </li>
    </ul>
  );
}
