import { createStore, combineReducers, applyMiddleware, compose } from 'redux';
import gameReducer from './game/reducer';
import { ADD_FAIL_VERB, REMOVE_FAIL_VERB } from './game/actions';

const rootReducer = combineReducers({
  game: gameReducer
});

const m22MiddleWare = store => next => action => {
  next(action);

  if (action.type === ADD_FAIL_VERB || action.type === REMOVE_FAIL_VERB) {
    const state = store.getState();
    if (Object.keys(state.game.fails).length)
      localStorage.setItem('VERBER_FAILED_TASKS', JSON.stringify(state.game.fails));
  }
};

const middleware = [m22MiddleWare];

const composeEnhancers =
  typeof window === 'object' && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({
        // Specify extension’s options like name, actionsBlacklist, actionsCreators, serialize...
      })
    : compose;

const store = createStore(rootReducer, composeEnhancers(applyMiddleware(...middleware)));

export default store;
